/* src/components/Dice.css */
.dice-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  position: relative;
}

.die {
  width: 100px;
  height: 100px;
  transform-style: preserve-3d;
  transition: transform 2s;
  position: relative;
  margin: 20px;
}

.die.rolling {
  animation: roll 2s infinite;
}

@keyframes roll {
  0% { transform: rotateX(0deg) rotateY(0deg); }
  100% { transform: rotateX(360deg) rotateY(360deg); }
}

.face {
  position: absolute;
  width: 100px;
  height: 100px;
  background: #ffcc00;
  border: 1px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  transform-style: preserve-3d;
}

/* Apply 3D transformation to each face */
.face-1 { transform: rotateY(0deg) translateZ(50px); }
.face-2 { transform: rotateY(72deg) translateZ(50px); }
.face-3 { transform: rotateY(144deg) translateZ(50px); }
.face-4 { transform: rotateY(216deg) translateZ(50px); }
.face-5 { transform: rotateY(288deg) translateZ(50px); }
.face-6 { transform: rotateX(72deg) translateZ(50px); }
.face-7 { transform: rotateX(-72deg) translateZ(50px); }
.face-8 { transform: rotateX(72deg) rotateY(72deg) translateZ(50px); }
.face-9 { transform: rotateX(-72deg) rotateY(72deg) translateZ(50px); }
.face-10 { transform: rotateX(72deg) rotateY(144deg) translateZ(50px); }
.face-11 { transform: rotateX(-72deg) rotateY(144deg) translateZ(50px); }
.face-12 { transform: rotateX(72deg) rotateY(216deg) translateZ(50px); }
.face-13 { transform: rotateX(-72deg) rotateY(216deg) translateZ(50px); }
.face-14 { transform: rotateX(72deg) rotateY(288deg) translateZ(50px); }
.face-15 { transform: rotateX(-72deg) rotateY(288deg) translateZ(50px); }
.face-16 { transform: rotateY(36deg) rotateZ(90deg) translateZ(50px); }
.face-17 { transform: rotateY(108deg) rotateZ(90deg) translateZ(50px); }
.face-18 { transform: rotateY(180deg) rotateZ(90deg) translateZ(50px); }
.face-19 { transform: rotateY(252deg) rotateZ(90deg) translateZ(50px); }
.face-20 { transform: rotateY(324deg) rotateZ(90deg) translateZ(50px); }

.roll-button {
  margin-top: 30px;
}
